.list {
 overflow:hidden;
 max-height: 0;
 -webkit-transition: max-height 1s ease;
 -o-transition: max-height 1s ease;
 transition: max-height 1s ease;
}

.list__item {
  text-transform: uppercase;
  font-size: .9rem;
}

.list__item--selected {
  color: var(--color-secondary);
  list-style-type: disc;
}


