.filters {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.filters__dropdawn {
  padding: 0;
  list-style: none;
  text-transform: uppercase;
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.filters__dropdawn__item {
  font-weight: 700;
  color: var(--color-primary);
  letter-spacing: .1em;
}

.filters__dropdawn:hover .list {
  max-height: 1rem;
}

@media (min-width: 700px) {
  .filters {
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
  .filters__dropdawn {
    padding-left: 3rem;
  }
}


